import React, {useState} from "react";
import s from "./status.module.css"
import {useFormik} from "formik";

let StatusHook = ({status,updateStatus}: { status: string, updateStatus: any }) => {
    let [editMode, setEditMode] = useState(false)
    let activatedEditMode = () => {
        setEditMode(true)
    }
    let deactivatedEditMode = () => {
        debugger
        setEditMode(false)
        updateStatus(formik.values.email)
    }

    const formik = useFormik({
        initialValues: {
            email: status,
        },
        onSubmit: values => {},
    });
    return <div className={s.status}>
        {editMode ? <div>
                <input
                    className={s.input}
                    id="status"
                    name="status"
                    onChange={formik.handleChange}
                    value={formik.values.email}
                    autoFocus={true}
                    onBlur={deactivatedEditMode}
                />

            </div> :
            <div className={s.textStatus} onClick={activatedEditMode}>{`${status || "none"}`}</div>}
    </div>

}
export default StatusHook
